<template>
    <el-dialog title="项目信息"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name,'pageDataRef')"
    >
        <el-form :model="pageData" :rules="pageDataRules" ref="pageDataRef" :inline="true" label-width="160px" size="small">
            <el-form-item label="代理服务器id" prop="agent_server_id">
                <!--<el-input v-if="pageInfo.pageModel == 'edit'" :disabled="true" v-model="pageData.project" style="width:260px"></el-input>-->
                <!--<el-input v-else v-model="pageData.project" style="width:260px"></el-input>-->
                <el-input v-model="pageData.agent_server_id" style="width:260px"></el-input>
            </el-form-item>
            <el-form-item label="代理名称" prop="agent_name">
                <el-input v-model="pageData.agent_name" style="width:260px"></el-input>
            </el-form-item>
            <el-form-item label="代理网段" prop="agent_network_area">
                <el-input v-model="pageData.agent_network_area" style="width:260px"></el-input>
            </el-form-item>
            <el-form-item label="代理外网ip" prop="agent_outer_ip">
                <el-input v-model="pageData.agent_outer_ip" style="width:260px"></el-input>
            </el-form-item>
            <el-form-item label="代理内网ip" prop="agent_inner_ip">
                <el-input v-model="pageData.agent_inner_ip" style="width:260px"></el-input>
            </el-form-item>
            <el-form-item label="代理服务器位置" prop="agent_location">
                <el-input v-model="pageData.agent_location" style="width:260px"></el-input>
            </el-form-item>
            <el-form-item label="代理机房" prop="agent_motor_room">
                <el-input v-model="pageData.agent_motor_room" style="width:260px"></el-input>
            </el-form-item>
            <el-form-item label="代理安全码" prop="agent_safety_code">
                <el-input v-model="pageData.agent_safety_code" style="width:260px"></el-input>
            </el-form-item>
            <el-form-item label="备注" prop="remark">
                <el-input type="textarea" v-model="pageData.remark" style="width:660px"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="onClose(module_name,'pageDataRef')">关 闭</el-button>
            <el-button v-if="pageInfo.pageModel === 'add'" type="primary" @click="onSubmit">确 定</el-button>
            <el-button v-else-if="pageInfo.pageModel === 'edit'" type="primary" @click="onUpdate">更 新</el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default {
        name: 'serverAgentManageInfo',
        data(){
            return {
                module_name: 'serverAgentManage',
                refresh_page_method: 'refresh_' + this.module_name,
                project_type_code_list: null,
                project_env_code_list: null,
                project_state_code_list: null,
                pageData: {
                    agent_server_id: '',
                    agent_name: '',
                    agent_outer_ip: '',
                    agent_inner_ip: '',
                    agent_location: '',
                    agent_motor_room: '',
                    agent_network_area: '',
                    agent_safety_code: '',
                    remark: ''
                },
                pageDataRules: {
                    agent_name: [{required: true, message: '请输入代理名称', trigger: 'blur'}],
                }
            }
        },
        computed: {
            pageInfo(){
                return this.$setPageInfo(this.module_name)
            }
        },
        methods: {
            async r_insert_info(item) // 插入信息
            {
                let R = new this.$http();
                let {data} = await R.post(item, 'OAMP_insert_agent_info');
                if (data != null && data !== 'null')
                    return data.data;
            },
            async r_update_info(item) // 修改信息
            {
                let R = new this.$http();
                let {data} = await R.post(item, 'OAMP_update_agent_info');
                if (data != null && data !== 'null')
                    return data;
            },
            onSubmit() // 提交按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async(valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.r_insert_info(item);
                        if (res != null) {
                            _this.$message.success('新增代理信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('新增代理信息失败');
                        }
                    }
                });
            },
            onUpdate() // 更新按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async(valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.r_update_info(item);
                        if (res != null) {
                            _this.$message.success('修改代理信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('修改代理信息失败');
                        }
                    }
                });
            }
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


