<template>
    <div>
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="代理服务器名称" prop="project">
                <el-input v-model="searchFormData.server_name" placeholder="请输入代理服务器名称"></el-input>
            </el-form-item>
            <div class="v-search-opt">
                <div class="v-pagination">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="searchFormData.pageNum"
                            :page-size="searchFormData.pageSize"
                            :total="searchFormData.pageCount"
                            :page-sizes="[14, 20, 50, 100]"
                            layout="total, sizes, prev, pager, next, jumper"
                    >
                    </el-pagination>
                </div>
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <!--<el-button size="mini" type="primary" @click="onAdd(module_name)"><i class="iconfont icon-xinzeng"></i> 新增</el-button>-->
                </div>
            </div>
        </el-form>
        <hr>
        <!--<el-table :data="tableData"  :stripe="true" :border="true"  height="708">-->
            <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)" >
            <el-table-column fixed type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column
                    prop="server_name"
                    label="代理服务器名称"
                    width="260">
            </el-table-column>
            <el-table-column
                    prop="server_outer_ip"
                    label="代理外网ip"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="server_inner_ip"
                    label="代理内网ip"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="agent_network_area"
                    label="代理网段"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="agent_location"
                    label="代理服务器位置"
                    width="280">
            </el-table-column>
            <el-table-column
                    prop="agent_motor_room"
                    label="代理机房"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="agent_safety_code"
                    label="代理安全码"
            >
            </el-table-column>

            <el-table-column fixed="right" label="操作" width="60">
                <template slot-scope="scope">
                    <el-col  offset="4" :span="12">
                        <el-tooltip class="item" effect="dark" content="代理配置" placement="left">
                            <el-button @click.native.prevent="on_agent_configure(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-bushupeizhi"></i></el-button>
                        </el-tooltip>
                    </el-col>
                    <!--<el-col :span="12">-->
                        <!--<el-tooltip class="item" effect="dark" content="代理列表" placement="left">-->
                            <!--<el-button @click.native.prevent="on_list_configure(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-peizhiqingdan"></i></el-button>-->
                        <!--</el-tooltip>-->
                    <!--</el-col>-->
                    <!--<el-col :span="5">-->
                        <!--<el-tooltip class="item" effect="dark" content="查看" placement="left">-->
                            <!--<el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-chakan"></i></el-button>-->
                        <!--</el-tooltip>-->
                    <!--</el-col>-->
                    <!--<el-col :span="5">-->
                        <!--<el-tooltip class="item" effect="dark" content="修改" placement="left">-->
                            <!--<el-button @click.native.prevent="onEdit(module_name,scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-xiugai"></i></el-button>-->
                        <!--</el-tooltip>-->
                    <!--</el-col>-->
                    <!--<el-col :span="4">-->
                        <!--<el-tooltip class="item" effect="dark" content="删除" placement="left">-->
                            <!--<el-button @click.native.prevent="onDelete(scope.$index, tableData)" type="text" size="small"><i class="iconfont icon-shanchu"></i></el-button>-->
                        <!--</el-tooltip>-->
                    <!--</el-col>-->

                </template>
            </el-table-column>

        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'serverAgentManageList',
        data() {
            return {
                module_name: 'serverAgentManage',
                refresh_page_method: 'refresh_' + this.module_name,
                project_type_code_list: null,
                project_env_code_list: null,
                project_state_code_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 14,
                    pageCount: 1,
                    server_name: '',
                    order: '+agent_name'
                },
                tableData: []
            }
        },
        created() {
            this.$store.state.Apps.$on(this.refresh_page_method, () => {
                this.search();
            });
        },
        async mounted() {
            this.search();
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            async r_search_list(searchData) {
                let R = new this.$http();
                let {data} = await R.post(searchData, 'OAMP_search_agent_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data
            },
            async r_delete_info(rowData) // 删除项目信息
            {
                let R = new this.$http();
                let {data} = await R.post(rowData, 'OAMP_delete_agent_info');
                return data != null && data !== 'null';
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            search()
            {
                let searchData = this.searchFormData;
                this.r_search_list(searchData)
            },
            // onDelete(index, rows)  // 删除
            // {
            //     let rowData = rows[index];
            //     if (this.r_delete_info(rowData)) {
            //         rows.splice(index, 1);
            //         this.$message.success('删除数据成功');
            //         this.search();
            //     }
            //     else {
            //         this.$message.error('删除数据失败');
            //     }
            // },
            on_agent_configure(index, rows)  // 配置代理信息
            {
                this.$router.push({
                    name: 'agentConf',
                    params: {
                        serverId: rows[index]['_id']
                    }
                });

            },
            // on_list_configure(index, rows)  // 查看代理列表
            // {
            //     console.log(rows[index])
            // }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



